import {
	mapState,
	mapMutations
} from 'vuex'
import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiActionsheet from "@/components/actionsheet/actionsheet"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiActionsheet
	},
	data() {
		return {
			historyData: [],
			historyShow: true,
			keyword: "",
			showActionSheet: false,
			tips: "确认清空搜索历史吗？"
		}
	},
	methods: {
		back: function() {
			uni.navigateBack();
		},
		cleanKey: function() {
			this.keyword = ''
		},
		closeActionSheet: function() {
			this.showActionSheet = false
		},
		openActionSheet: function() {
			this.showActionSheet = true
		},
		chooseKeywords: function(e){
			this.keyword = e;
		},
		handleSearch(){
			if(this.keyword.trim() == ''){
				uni.showToast({
					icon: 'none',
					title: '搜索条件不能为空',
				});
			}else{
				uni.setStorageSync('searchKeyword', this.keyword);
				uni.switchTab({
					url: '/pages/product/product'
				});
			}
		},
		itemClick: function(e) {
			let index = e.index;
			if(index == 0){
				this.showActionSheet = false;
				this.$http.post('/mall/app/search/history/clear', {
					...this.baseInfo,
					accountId: this.userInfo.accountId
				})
				.then( res => {
					if(res.code == 0){
						this.getHistoryData();
					}else{
						console.log('searchPage.vue-- clear接口清空数据失败');
					}
				})
				.catch( err => {
					console.log('searchPage.vue-- clear接口清空数据错误');
				})
			}
		},
		unique(arr) {
			var list = [];
			arr.map(item => {
				list.push(item.keyword);
			});
			return [...new Set(list)];
		},
		getHistoryData(){
			this.$http.post('/mall/app/search/history/query', {
				...this.baseInfo,
				accountId: this.userInfo.accountId
			})
			.then( res => {
				if(res.code == 0){
					if(res.result.list){
						this.historyData=this.unique(res.result.list);
						console.log(this.historyData)
					}else{
						this.historyShow=false;
					}
				}else{
					console.log('searchPage.vue-- query接口获取数据失败');
				}
			})
			.catch( err => {
				console.log('searchPage.vue-- query接口获取数据错误');
			})
		}
	},
	computed: {
		...mapState("common", ['baseInfo', 'userInfo'])
	},
	onLoad: function(){
		this.getHistoryData();
	}
}
